"use client";
import { useState } from "react";

const Categorias = () => {
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);

  const handleDropdownToggle = (category: string) => {
    if (openDropdown === category) {
      setOpenDropdown(null);
    } else {
      setOpenDropdown(category);
    }
  };

  const categories = [
    {
      name: "Alimentos",
      items: [
        "Frutas frescas",
        "Hortalizas frescas",
        "Carne y Vísceras de res",
        "Carne y Vísceras de cerdo",
        "Carne de ave",
        "Pescados y mariscos",
        "Huevo",
        "Leche fresca",
        "Leche procesada",
        "Derivados de leche",
        "Pan",
        "Tortillas y derivados del maíz",
        "Galletas",
        "Galletas pastas y harinas de trigo",
        "Azúcar",
        "Aceites y grasas veg. comestibles",
        "Grasas animales comestibles",
        "Arroz y Cereales preparados",
        "Legumbres secas",
        "Alimentos cocinados f/casa",
        "Carnes Frías secas y embutidos",
        "Pescados y mariscos en conserva",
        "Frutas y legumbres procesadas",
        "Condimentos",
      ],
    },
    {
      name: "Bebidas",
      items: [
        "Refrescos envasados",
        "Café",
        "Té",
        "Cerveza",
        "Vinos y licores",
      ],
    },
    {
      name: "Hogar y Limpieza",
      items: [
        "Detergentes y productos similares",
        "Artículos de papel P/higiene personal",
        "Accesorios domésticos",
        "Utensilios domésticos",
        "Aparatos eléctricos",
        "Aparatos Electrónicos",
      ],
    },
    {
      name: "Cuidado Personal",
      items: ["Artículos para el cuidado personal", "Medicamentos"],
    },
    {
      name: "Otros",
      items: [
        "Chocolates y golosinas",
        "Cigarrillos",
        "Material escolar",
        "Artículos de esparcimiento (Juguetes)",
        "Productos de temporada (Navideños)",
      ],
    },
  ];

  return (
    <div
      className="flex justify-center items-center
  gap-15 text-lg pt-4 pb-4"
    >
      <a
        href="#mejores-ofertas"
        className="cursor-pointer hover:text-red-600 transition-colors mx-4"
      >
        Ofertas
      </a>
      {categories.map((category) => (
        <div key={category.name} className="relative mx-4">
          <button
            onClick={() => handleDropdownToggle(category.name)}
            className="flex items-center hover:text-red-600 transition-colors"
          >
            {category.name}
            <svg
              className={`ml-1 w-4 h-4 transition-transform ${
                openDropdown === category.name ? "rotate-180" : ""
              }`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M19 9l-7 7-7-7"
              ></path>
            </svg>
          </button>
          {openDropdown === category.name && (
            <div className="absolute z-50 mt-2 w-64 bg-white rounded-md shadow-lg py-1 text-base max-h-96 overflow-y-auto left-0">
              {category.items.map((item) => (
                <a
                  key={item}
                  href={`#${item.toLowerCase().replace(/\s+/g, "-")}`}
                  className="block px-4 py-2 text-gray-800 hover:bg-gray-100"
                >
                  {item}
                </a>
              ))}
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default Categorias;
